.CircleContainer{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    height: 40vh;

}

.BpmDisplayContainer{
    font-size: 2.7rem;
    margin: 1vh 0;
    .InputContainer{
        display: inline-block;
        width: 4.5vw;
    }
    input{
        color:white;
        text-align: center;
        width: 100%;
        background:none;
        font-size: 2.7rem;
        border:none;
        outline:none;
        transition: 0.2s;
        padding: 1px;
        border: 0px solid #d1fb42;
    }
    input:hover{
     border-bottom: 2px solid #d1fb42;
    }

}

.PlayFade {
  opacity: 0.1;
}

.Pulse {
    opacity: 1;
    transform: scale(0.87);
}

    @media all and (max-width: 1024px) and (min-width: 768px) { 
    .BpmDisplayContainer{
        font-size: 3rem;
        .InputContainer{
            width: 11vw;
        }
        input{
            font-size: 3rem;
        }
    }
 }
   @media all and (max-width: 767px) and (min-width: 400px) { 
    .BpmDisplayContainer{
        font-size: 3rem;
        .InputContainer{
            width: 17vw;
        }
        input{
            font-size: 3rem;
        }
      }
    }

    @media all and (max-width: 400px) and (min-width: 321px) { 
        .BpmDisplayContainer{
            font-size: 3rem;
            .InputContainer{
                width: 20vw;
            }
            input{
                font-size: 3rem;
            }
          }
        }

    @media all and (max-width: 320px) { 
        .BpmDisplayContainer{
            font-size: 2.5rem;
            .InputContainer{
                width: 25vw;
            }
            input{
                font-size: 2.5rem;
            }
        }
    }
